export default {
  name: "naver-link",
  props: {
    type: String,
    matchSchedule: Object,
    employCost: String
  },
  data() {
    return {
      teamInfo: {}
    };
  },
  methods: {
    // U03-2
    getInfoForWrite() {
      let teamId = JSON.parse(sessionStorage.getItem("userInfo")).teamId;
      let email = JSON.parse(sessionStorage.getItem("userInfo")).email;
      this.$axios
        .get("/team/2/" + teamId + "/" + email)
        .then(response => {
          this.teamInfo = response.data;
          this.write();
        })
        .catch(() => {
          alert(" .");
        })
        .finally(() => {
          this.close();
        })
    },
    // U03-1
    close() {
      this.$emit("close");
    },
    //U03-5
    shareLink() {
      //3.   /url 
      let title = "";
      if (this.matchSchedule.matchType.split(":")[0] < 11) {
        title = this.matchSchedule.matchType + " ";
      } else {
        title = this.matchSchedule.matchType + " ";
      }

      let url = "";
      switch (this.type) {
        case "search":
          url = "search";
          title += "  ";
          break;
        case "assign":
          url = "assign";
          title += "  ";
          break;
        case "employ":
          url = "employ?employ_id=4";
          title += "   ";
          break;
      }

      let encodedUrl = encodeURI(
        encodeURIComponent("http://localhost:8080/" + url)
      );
      let encodedTitle = encodeURI(title);
      var shareURL =
        "https://share.naver.com/web/shareView.nhn?url=" +
        encodedUrl +
        "&title=" +
        encodedTitle;
      window.open(shareURL);
    },
    // U03-3
    write() {
      // 1.     
      // eslint-disable-next-line prettier/prettier
      let matchDateTime = this.matchSchedule.startTime.slice(0, 13) + " ~ " + this.matchSchedule.endTime.slice(11, 13);
      let stadiumName = this.matchSchedule.stadiumName;
      let teamManner = this.covertIntToString(this.teamInfo.teamManner);
      let teamAbility = this.covertIntToString(this.teamInfo.teamAbility);
      let phoneNum = this.teamInfo.teamMembers[0].user.phoneNum;
      let originCost = this.matchSchedule.cost;
      let cost = this.classifyCost();
      let introduction = this.showIntroduction();

      // 2.   
      let text = "1. \n\n";
      text += "   " + matchDateTime + "\n\n";
      text += "2. \n\n";
      text += "   " + stadiumName + "\n\n";
      text += "3. &\n\n";
      text += "   " + ": " + teamAbility + ", : " + teamManner + "\n\n";
      text += "4. \n\n";
      text += "   " + phoneNum + "\n\n";
      text += "5.  \n\n";
      text += "   " + originCost + " \n\n";
      text += "6. \n\n";
      text += "   " + cost + " \n\n";
      text += "7. \n\n";
      text += "   " + introduction;

      // 2. 
      this.copy(text);
    },
    // U03-4
    copy(text) {
      var createInput = document.createElement("textarea");
      //     input  .
      document.getElementById("textDiv").appendChild(createInput);
      //  input  value     .
      createInput.value = text;
      //    
      createInput.select();
      document.execCommand("copy");
      //   input  .
      document.getElementById("textDiv").removeChild(createInput);
      // api 
      this.shareLink();
    },
    covertIntToString(value) {
      if (value < 2) {
        return "";
      } else if (value < 4) {
        return "";
      } else if (value < 6) {
        return "";
      } else if (value < 8) {
        return "";
      } else {
        return "";
      }
    },
    classifyCost() {
      switch (this.type) {
        case "search":
          var cost = (this.matchSchedule.cost / 2).toFixed(2);
          return Math.ceil(cost * 100) / 100;
        case "assign":
          return this.matchSchedule.cost;
        case "employ":
          return this.employCost;
      }
    },
    showIntroduction() {
      let uniformType = "";
      if (this.teamInfo.uniformType == 0) uniformType = "";
      else uniformType = "";
      let introduction = "";
      introduction += this.teamInfo.foundingDate + "  ";
      introduction += this.teamInfo.teamName + " ";
      introduction += this.teamInfo.area + "   ";
      introduction += this.teamInfo.uniformColor + " ";
      introduction += uniformType + " . ";

      return introduction;
    }
  }
};
